html {
  overflow-y: scroll;
}

body {
  font-family: Montserrat, sans-serif;
}

.nav-bar {
  background-color: #593f62;
  align-items: center;
  width: 100%;
  min-height: 75px;
  display: flex;
}

.nav-bar img {
  aspect-ratio: 1;
  width: 50px;
}

.todo-wrapper {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: min(730px, 95%);
  height: 100%;
  min-height: 250px;
  margin: 0 auto;
  display: flex;
}

.todo-card {
  background: #fefefe;
  border-radius: 2rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 600px;
  min-height: 660px;
  margin: 50px 0;
  padding: 70px 80px;
  display: flex;
  box-shadow: 0 22px 70px 4px #0000008f;
}

@media only screen and (max-width: 468px) {
  .todo-card {
    min-height: 250px;
    padding: 20px 30px;
  }
}

.todo-card h1 {
  color: #fff;
  letter-spacing: .5px;
  background-color: #ff1654;
  border-radius: 8px;
  margin-bottom: 40px;
  padding: 5px 30px;
  font-family: Montserrat, sans-serif;
  font-size: 32px;
  line-height: 160%;
  display: inline-block;
}

@media only screen and (max-width: 468px) {
  .todo-card h1 {
    font-size: 24px;
  }
}

.toDoListWrappers {
  flex-direction: column;
  width: 100%;
  height: 80%;
  margin-top: 20px;
  display: flex;
  overflow-y: scroll;
}

@media only screen and (max-width: 468px) {
  .toDoListWrappers {
    height: 70%;
  }
}

.button {
  background-color: #0000;
  border: none;
  width: 40px;
  height: 40px;
}

.button:hover {
  opacity: .7;
  transition: all .1s;
}

@media only screen and (max-width: 478px) {
  .button {
    width: 30px;
    height: 30px;
  }
}

::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background: #dbdbdb;
}

::-webkit-scrollbar-thumb:hover {
  background: #bfbfbf;
}

